html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}

body.modal-open {
  overflow: hidden;
}
.section-title {
  margin-top: 0;
  margin-bottom: 24px;
  font-family: Caveat Brush;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 2.71;
  text-align: center;
  color: var(--main-text-color);
  @media screen and (min-width: 1280px) {
    font-size: 36px;
    line-height: 1.81;
  }
}
.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.btn {
  display: inline-block;
  border-radius: 4px;
  min-width: 158px;
  padding: 8px 40px;
  font-family: inherit;
  border: none;
  cursor: pointer;
  background-color: #ab816c;
  border-radius: 25px;
  transition: background-color var(--animation), color var(--animation);

  &:focus,
  &:hover {
    background-color: rgba(171, 129, 108, 0.5);
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1),
      color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  }
}

.btn__text {
  font-family: Caveat Brush;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 2.71px;
  text-align: center;
  // border: 1px solid #000000;
}
.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
  width: 100%;

  @media screen and (min-width: 320px) {
    width: 320px;
  }

  @media screen and (min-width: 480px) {
    width: 480px;
  }

  @media screen and (min-width: 768px) {
    width: 768px;
  }

  @media screen and (min-width: 1280px) {
    width: 1280px;
  }
}
